import React from "react";
import { IconButton, Typography } from "@material-ui/core";
import { useState, useEffect } from "react";
import { addFamily, deleteFamily } from "../../Functions/admin";
import { getFamilyList, getGharanaList } from "../../Functions/app";
import AddIcon from "@material-ui/icons/Add";
import { useParams } from "react-router";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";

export default function AddFamilies() {
  const [families, setFamilies] = useState([]);
  const [gharana, setGharana] = useState("");
  const [loading, setLoading] = useState(true);
  const [filterString, setFilterString] = useState("");
  const gharanaId = useParams().gharanaId;
  useEffect(() => {
    const x = async () => {
      let e = await getGharanaList();
      console.log(e);
      setGharana(e.filter((e) => e.id === gharanaId)[0].name);
      e = await getFamilyList(gharanaId);
      setFamilies(e);
      setLoading(false);
    };
    x();
  }, []);

  return (
    <div className="row center">
      <div className="col s12">
        <div className="col s4 m2 l2 input-field">
          <IconButton
            className=" blue white-text"
            onClick={() => {
              window.history.back();
            }}
          >
            <ArrowBackIcon />
          </IconButton>
        </div>
        <div className="col s8 m10 l10 input-field">
          <Typography variant="h4" align="left">
            Gharana - {gharana}
          </Typography>
        </div>
      </div>
      <div className="col s12 m6 l3">
        <div className="card-panel white blue-text" style={{ height: "140px" }}>
          <div className=" col s12 m12 l12">
            <input
              type="text"
              id="familyName"
              placeholder="Family Name"
              value={filterString}
              onChange={(e) => {
                setFilterString(e.target.value);
              }}
            />
          </div>
          <div className="input-field col s12 m12 l12">
            <Typography
              variant="h4"
              align="center"
              className="btn-floating blue"
              onClick={() => {
                if (filterString === "") {
                  alert("please enter name");
                } else {
                  setLoading(true);
                  addFamily(gharanaId, filterString)
                    .then((e) => {
                      window.location.reload();
                    })
                    .catch((e) => {
                      alert("Family with that name already exists");
                    });
                  setFilterString("");
                }
              }}
            >
              <AddIcon />
            </Typography>
          </div>
        </div>
      </div>
      {families
        .filter((e) => {
          return e.name !== undefined
            ? e.name.toLowerCase().includes(filterString.toLowerCase())
            : false;
        })
        .map((e, i) => {
          return (
            <div className="col s12 m6 l3" key={i}>
              <div
                className="card-panel blue white-text center"
                style={{ height: "140px" }}
                title={e.name}
              >
                <Typography variant="h4" align="center" className="truncate">
                  {e.name}
                </Typography>
                <br />
                <div className="col s12 m12 l12">
                  <div className="col s6 m6 l6 center">
                    <div
                      className="btn white blue-text"
                      onClick={() => {
                        window.location.href += e.id;
                      }}
                    >
                      View
                    </div>
                  </div>
                  <div className="col s6 m6 l6 center">
                    <div
                      className="btn red white-text"
                      onClick={() => {
                        if (
                          window.confirm(
                            `This is an Irreversible Process are you sure you want to delete family ${e.name}?`
                          )
                        ) {
                          setLoading(true);
                          deleteFamily(e.id).then((e) => {
                            window.location.reload();
                          });
                        }
                      }}
                    >
                      delete
                    </div>
                  </div>
                </div>
              </div>
            </div>
          );
        })}
    </div>
  );
}
