import { Typography } from "@material-ui/core";
import { Route, BrowserRouter as Router, Switch } from "react-router-dom";
import AddChildren from "./Pages/Admin/AddChildren";
import AddFamilies from "./Pages/Admin/AddFamilies";
import AddGharanas from "./Pages/Admin/AddGharanas";

function Admin() {
  return (
    <Router basename={process.env.PUBLIC_URL}>
      <Switch>
        <Route path="/" exact>
          <AddGharanas />
        </Route>
        <Route path="/:gharanaId" exact>
          <AddFamilies />
        </Route>
        <Route path="/:gharanaId/:familyId" exact>
          <AddChildren />
        </Route>
        <Route>
          <Typography variant="h4" align="center">
            Error Page Not Found
          </Typography>
        </Route>
      </Switch>
    </Router>
  );
}

export default Admin;
