import { link } from "./app";

export const addGharana = async (gharanaName) => {
  let href = `${link}?gharana=true`;
  let body = new FormData();
  body.append("name", gharanaName);
  let response = await fetch(href, { method: "post", body: body });
  return await response.text();
};

export const deleteGharana = async (id) => {
  let href = `${link}?gharana=true`;
  let body = new FormData();
  body.append("id", id);
  let response = await fetch(href, { method: "post", body: body });
  return await response.text();
};

export const addFamily = async (gharanaId, familyName) => {
  let href = `${link}?family=true`;
  let body = new FormData();
  body.append("name", familyName);
  body.append("gharanaId", gharanaId);

  let response = await fetch(href, { method: "post", body: body });
  return await response.text();
};

export const deleteFamily = async (id) => {
  let href = `${link}?family=true`;
  let body = new FormData();
  body.append("id", id);
  let response = await fetch(href, { method: "post", body: body });
  return await response.text();
};

export const addChild = async (familyId, parentId, name) => {
  let href = `${link}?children=true`;
  let body = new FormData();
  body.append("parentId", parentId);
  body.append("familyId", familyId);
  body.append("name", name);
  let response = await fetch(href, { method: "post", body: body });
  return await response.json();
};

export const deleteChild = async (id) => {
  let href = `${link}?children=true`;
  let body = new FormData();
  body.append("id", id);
  let response = await fetch(href, { method: "post", body: body });
  return await response.text();
};
