import React from "react";
import { IconButton, Typography } from "@material-ui/core";
import { useState, useEffect } from "react";
import { addGharana, deleteGharana } from "../../Functions/admin";
import { getGharanaList } from "../../Functions/app";
import AddIcon from "@material-ui/icons/Add";
import Loading from "../Loading";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
export default function AddGharanas() {
  const [gharanas, setGharanas] = useState([]);
  const [loading, setLoading] = useState(true);
  const [filterString, setFilterString] = useState("");

  useEffect(() => {
    getGharanaList().then((res) => {
      setGharanas(res);
      setLoading(false);
    });
  }, []);

  return loading ? (
    <Loading />
  ) : (
    <div className="row center">
      <div className="col s12 input-field">
        <div className="col s3 ">
          <IconButton
            className=" blue white-text"
            onClick={() => {
              window.location.href = "http://ponkshe.com/adminpanel/";
            }}
          >
            <ArrowBackIcon />
          </IconButton>
        </div>
      </div>
      <div className="col s12 m6 l3">
        <div className="card-panel white blue-text" style={{ height: "140px" }}>
          <div className=" col s12 m12 l12">
            <input
              type="text"
              id="gharanaName"
              placeholder="Gharana Name"
              value={filterString}
              onChange={(e) => {
                setFilterString(e.target.value);
              }}
            />
          </div>
          <div className="input-field col s12 m12 l12">
            <Typography
              variant="h4"
              align="center"
              className="btn-floating blue"
              onClick={() => {
                if (filterString === "") {
                  alert("please enter name");
                } else {
                  setLoading(true);
                  addGharana(filterString)
                    .then((e) => {
                      window.location.reload();
                    })
                    .catch((e) => {
                      alert("Gharana with that name already exists");
                    });
                }
              }}
            >
              <AddIcon />
            </Typography>
          </div>
        </div>
      </div>
      {gharanas.length === 0 ? (
        <div className="col s12">
          <Typography
            variant="h4"
            align="center"
            className="card-panel col s12 m6 l3"
            style={{ height: "140px" }}
          >
            No Gharanas Found
          </Typography>
        </div>
      ) : (
        gharanas
          .filter((e) => {
            return e.name !== undefined
              ? e.name.toLowerCase().includes(filterString.toLowerCase())
              : false;
          })
          .map((e, i) => {
            return (
              <div className="col s12 m6 l3" key={i}>
                <div
                  className="card-panel blue white-text center"
                  style={{ height: "140px" }}
                  title={e.name}
                >
                  <Typography variant="h4" align="center" className="truncate">
                    {e.name}
                  </Typography>
                  <br />
                  <div className="col s12 m12 l12">
                    <div className="col s6 m6 l6 center">
                      <div
                        className="btn white blue-text"
                        onClick={() => {
                          window.location.href = e.id + "/";
                        }}
                      >
                        View
                      </div>
                    </div>
                    <div className="col s6 m6 l6 center">
                      <div
                        className="btn red white-text"
                        onClick={() => {
                          if (
                            window.confirm(
                              `This is an Irreversible Process are you sure you want to delete Gharana ${e.name}?`
                            )
                          ) {
                            deleteGharana(e.id).then((e) => {
                              window.location.reload();
                            });
                          }
                        }}
                      >
                        delete
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            );
          })
      )}
    </div>
  );
}
