import {
  IconButton,
  InputLabel,
  MenuItem,
  Select,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Typography,
} from "@material-ui/core";
import DeleteIcon from "@material-ui/icons/Delete";
import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import {
  getChildren,
  getFamilyList,
  getGharanaList,
} from "../../Functions/app";
import { addChild, deleteChild } from "../../Functions/admin";

import Loading from "../Loading";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";

export default function AddChildren() {
  const [loading, setLoading] = useState("");
  const [isRoot, setIsRoot] = useState(true);
  const [data, setData] = useState([]);
  const [parent, setParent] = useState("");
  const [familyName, setFamilyName] = useState("");
  const [gharana, setGharana] = useState("");
  const familyId = useParams().familyId;
  const gharanaId = useParams().gharanaId;

  useEffect(() => {
    getGharanaList().then((e) => {
      setGharana(e.filter((e) => e.id === gharanaId)[0].name);
    });
    getFamilyList(gharanaId).then((e) => {
      setFamilyName(e.filter((x) => x.id === familyId)[0].name);
    });
    getChildren(familyId).then((e) => {
      if (e.length !== 0) {
        setIsRoot(false);
      }
      setData(e);
      setLoading(false);
    });
  }, []);

  return loading ? (
    <Loading />
  ) : (
    <div className="row container">
      <div className="col s12 m12 l12">
        <div className="col s4 m2 l2 input-field">
          <IconButton
            className=" blue white-text"
            onClick={() => {
              window.history.back();
            }}
          >
            <ArrowBackIcon />
          </IconButton>
        </div>
        <div className="col s8 m10 l10 input-field">
          <Typography variant="h4" align="left">
            Gharana - {gharana}
            {">"}
            Family - {familyName}
          </Typography>
        </div>
      </div>
      <div className={"input-field col s12 m6 l6 " + (isRoot ? "hide" : "")}>
        <select
          id="parent"
          className="col s12 m12 l12 active browser-default"
          onChange={(e) => {
            setParent(e.target.value);
          }}
          value={parent}
        >
          <option value="" disabled>
            Select the Parent
          </option>
          {data.map((e, i) => {
            return (
              <option key={i} value={e.id}>
                {e.name}
              </option>
            );
          })}
        </select>
      </div>
      <div className="row container">
        <div id="childList">
          <div className="input-field col s12 m6 l6">
            <input
              type="text"
              id="name1"
              name="name[]"
              placeholder={isRoot ? "Root Name" : "Child 1 Name"}
            />
          </div>
        </div>
        <div className={"col s12 m12 l12 center " + (isRoot ? "hide" : "")}>
          <div
            className="btn-floating blue valign-wrapper"
            onClick={() => {
              let list = document.getElementById("childList");
              let html = `
              <div className="input-field col s12 m6 l6">
                <input
                  type="text"
                  id="name${list.childElementCount + 1}"
                  name="name[]"
                  placeholder= "Child ${list.childElementCount + 1} Name"
                />
              </div>
            `;
              list.insertAdjacentHTML("beforeend", html);
            }}
          >
            +
          </div>
        </div>
      </div>
      <div className="col s12 m12 l12 input-field center">
        <div
          className="btn blue"
          onClick={async () => {
            let names = document.getElementsByName("name[]");
            if (parent === "" && !isRoot) {
              alert("please Select The parent");
            } else {
              for (let i = 0; i < names.length; i++) {
                let name = names[i].value;
                if (name !== "") {
                  let x = await addChild(familyId, parent ?? "null", name);
                }
              }
              window.location.reload();
            }
          }}
        >
          SUBMIT
        </div>
      </div>
      <div className="row container">
        <div className="col s12 m12 l12">
          <TableContainer>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell>Sr No.</TableCell>
                  <TableCell align="right">Parent</TableCell>
                  <TableCell align="right">Member Name</TableCell>
                  <TableCell align="right">Delete</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {data.map((row, e) => (
                  <TableRow key={e}>
                    <TableCell component="th" scope="row">
                      {e + 1}
                    </TableCell>
                    <TableCell align="right">
                      {row.parent ?? "isRoot"}
                    </TableCell>
                    <TableCell align="right">{row.name}</TableCell>
                    <TableCell align="right">
                      <DeleteIcon
                        onClick={() => {
                          if (
                            window.confirm(
                              `are you sure you want to DELETE ${row.name}?`
                            )
                          ) {
                            setLoading(true);
                            deleteChild(row.id).then((e) => {
                              window.location.reload();
                            });
                          }
                        }}
                      />
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </div>
      </div>
    </div>
  );
}
